<template>
    <div>
      <p class="title"><i class="el-icon-tickets"></i>10</p>
      <el-table
        border
        :data="tableData"
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          sortable
          prop="odd"
          label="">
        </el-table-column>
        <el-table-column
          prop="name"
          label="">
        </el-table-column>
        <el-table-column
          prop="status"
          label=""
          width="130">
        </el-table-column>
        <el-table-column
          prop="amount"
          label="">
        </el-table-column>
        <el-table-column
          prop="date"
          label=""
          sortable
          :filters="[{text: '2018-01-01', value: '2018-01-01'}, {text: '2018-01-02', value: '2018-01-02'}, {text: '2018-01-03', value: '2018-01-03'}, {text: '2018-01-04', value: '2018-01-04'}, {text: '2018-01-05', value: '2018-01-05'}]"
          :filter-method="filterHandler"
        >
        </el-table-column>
        <el-table-column
          prop="tag"
          label=""
          width="150"
          :filters="[{ text: '', value: '' }, { text: '', value: '' }]"
          :filter-method="filterTag"
          filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.tag === '' ? 'primary' : 'success'"
              disable-transitions>{{scope.row.tag}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
</template>

<script>
export default {
  name: 'maintable',
  data () {
    return {
      tableData: [{
        odd: '201801012345601',
        name: '',
        status: '',
        amount: '580',
        date: '2018-01-01',
        tag: ''
      }, {
        odd: '201801012345602',
        name: '',
        status: '',
        amount: '130',
        date: '2018-01-02',
        tag: ''
      }, {
        odd: '201801012345603',
        name: '',
        status: '',
        amount: '680',
        date: '2018-01-03',
        tag: ''
      }, {
        odd: '201801012345604',
        name: '',
        status: '',
        amount: '190',
        date: '2018-01-03',
        tag: ''
      }, {
        odd: '201801012345605',
        name: '',
        status: '',
        amount: '170',
        date: '2018-01-04',
        tag: ''
      }, {
        odd: '201801012345606',
        name: '',
        status: '',
        amount: '670',
        date: '2018-01-04',
        tag: ''
      }, {
        odd: '201801012345607',
        name: '',
        status: '',
        amount: '1780',
        date: '2018-01-04',
        tag: ''
      }, {
        odd: '201801012345608',
        name: '',
        status: '',
        amount: '3180',
        date: '2018-01-04',
        tag: ''
      }, {
        odd: '201801012345609',
        name: '',
        status: '',
        amount: '780',
        date: '2018-01-05',
        tag: ''
      }, {
        odd: '201801012345610',
        name: '',
        status: '',
        amount: '2130',
        date: '2018-01-05',
        tag: ''
      }]
    }
  },
  methods: {
    handleEdit (index, row) {
      console.log(index, row)
      this.$message({
        showClose: true,
        message: index, row,
        type: 'success'
      })
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$message({
        showClose: true,
        message: index, row,
        type: 'success'
      })
    },
    formatter (row, column) {
      return row.address
    },
    filterTag (value, row) {
      return row.tag === value
    },
    filterHandler (value, row, column) {
      const property = column['property']
      return row[property] === value
    }
  }
}
</script>

<style scoped>

</style>



